/* Reset básico e configuração de estilos globais */
body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #f0ecec;
    color: #333;
}

header, footer {
    background-color: #4a81bb;
    color: white;
    text-align: center;
    padding: 1rem;
}

main {
    padding: 2rem;
}

input[type="text"] {
    text-transform: uppercase; /* Transforma todas as letras em maiúsculas */
    letter-spacing: 0.1em; /* Adiciona espaçamento entre as letras */
    padding: 0.2rem;
    font-size: 0.9rem; 
}

button {
    padding: 0.2rem;
    font-size: 0.9rem; 
}

/* Estilização da div#results */
#results {
    display: flex;
    flex-wrap: wrap; /* Permite que os itens que não cabem em uma linha sejam movidos para a próxima linha */
    gap: 0.5rem; /* Espaçamento entre os itens */
    margin: 0.5rem 0;
}

.result-item {
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 0.2rem;
    flex: 1 1 calc(25% - 1rem); /* Ajusta o tamanho dos itens para ocupar 25% da largura menos o gap */
    box-sizing: border-box;
    text-align: center;
}

/* Estilização da mensagem */
#messages div {
    background-color: #e29c40;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 0.2rem;
    text-align: center;
}
